export default {
  formate (date, formateStr = 'yyyy-MM-dd hh:mm:ss') {
    if (!date) {
      return date
    }

    if (typeof date === 'number') {
      date = new Date(date)
    }
    if (typeof date === 'string') {
      date = new Date(date)
    }
    const o = {
      'M+': date.getMonth() + 1,                    //月份
      'd+': date.getDate(),                         //日
      'h+': date.getHours(),                        //小时
      'm+': date.getMinutes(),                      //分
      's+': date.getSeconds(),                      //秒
      'q+': Math.floor((date.getMonth() + 3) / 3),  //季度
      'S': date.getMilliseconds()                   //毫秒
    }
    if (/(y+)/.test(formateStr))
      formateStr = formateStr.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
    for (let k in o)
      if (new RegExp('(' + k + ')').test(formateStr))
        formateStr = formateStr.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)))
    return formateStr
  }
}



